.form{
    max-width: 1000px;
    margin: 0 auto;
    display: flex;
    margin-top: 40px;
    align-items: flex-end;
}
.top_btn{
    display: flex;
    gap: 32px;
}
.contact_form{
    display: flex;
    flex-direction: column;
    gap: 24px;

}
.form_controler{
    display: flex;
    flex-direction: column;
    width: 100%;
    position: relative;
   
}
.form_controler input{
    
    height: 40px;
    padding: 0 8px;
}
.form_controler textarea{
    
   
    padding: 8px;
}
.form_controler label{
    position: absolute;
    top: -12px;
    left: 10px;
    background-color: white;
    padding: 0 10px;

}
.contact_form form{
    display: flex;
    gap: 20px;
    padding-bottom: 20px;
    flex-direction: column;
}